.App{
    background-image: linear-gradient(to right,#00C6FF,#0072FF);
    width:100%;
    height:100vh;
    overflow: hidden;
    font-family: 'Roboto Mono', monospace;
}

@media(max-width:576px){
    .App{
        background-image: linear-gradient(to right,#00C6FF,#0072FF);
        width:100%;
        height:100vh;
        overflow: scroll;
    }
    
}